import React from 'react';
import TimeField from 'react-simple-timefield';

export default function Meeting(props) {
    const parentMeetings = props.schedules[props.parentIndex].meetings;
    const thisMeeting = parentMeetings[props.index];

    const handleChange = (event, value) => {
        const time = value;
        const innerIndex = event.target.getAttribute("data-bound") === "start" ? 0 : 1;
        let schedules = [...props.schedules];
        schedules[props.parentIndex].meetings[props.index][innerIndex] = time;
        props.setSchedules(schedules);
    }

    const handleDelete = () => {
        let schedules = [...props.schedules];
        const meetings = parentMeetings.filter((elem, index) => index !== props.index);
        schedules[props.parentIndex].meetings = meetings;
        props.setSchedules(schedules);
    }

    return (
        <div className="meeting time-pair">
            <TimeField onChange={handleChange} value={thisMeeting[0]} data-bound="start"/>
            <p>to</p>
            <TimeField onChange={handleChange} value={thisMeeting[1]} data-bound="end" />
            <button type="button" className="delete-meeting symbol" onClick={handleDelete}><i className="fas fa-times"></i></button>
        </div>
    )
}
