import React from 'react';
import Schedule from './Schedule.js';

export default function Editor(props) {

    const handleAdd = () => {
        let schedules = [...props.schedules];
        const index = schedules.length;
        schedules.push({
            name: `schedule${index + 1}`,
            bounds: ["00:00","00:00"],
            meetings: [["00:00","00:00"]]
        });
        props.setSchedules(schedules);
    }

    return (
        <div id="editor" className={props.calculated ? "invisible" : "visible"}>
            {props.schedules.map((elem, index) => <Schedule key={index} index={index} schedules={props.schedules} setSchedules={props.setSchedules} />)}
            <div id="create-new">
                <button type="button" id="add-schedule" onClick={handleAdd}><i className="fas fa-plus"></i></button>
            </div>
        </div>
    )
}
