import React from 'react';

export default function Info(props) {

    const handleChange = (e) => {
        props.setDuration(e.target.value);
    }

    const resultsTitle = props.success ? "Here are your available meeting times:" : "Sorry, no meetings found (reason listed below)."
    const resultsBody = (success) => {
        if (success) {
            return (<ul id="results-list">
                {props.possible.map((elem, index) => <li key={index}>
                    {elem[0]} to {elem[1]}
                </li>)}
            </ul>)
        } else {
            return (<p id="error">{props.possible}</p>)
        }
    }

    return (
        <div id="full-info" className={props.calculated ? "show-all" : "show-bar"}>
            <div id="results" className={props.calculated ? "mobile-show" : "mobile-hide"}>
                <h2 id="results-title">{resultsTitle}</h2>
                {resultsBody(props.success)}
            </div>
            <div id="info" className={props.calculated ? "right-side" : "left-side"}>
                <h1>Meeting Scheduler</h1>
                <h2>Instructions</h2>
                <p>Enter the schedule bounds underneath the schedule name. Enter currently filled meeting times under "Current Meetings". To add a new meeting, click the add meeting button. To remove any meeting, click the x at the end of the meeting.
                    <br /><br />To add a new schedule, click the grey "+" button. To remove a schedule, click the "-" button at the end of the schedule.
                    <br /><br />All times must be in military time format, from 00:00 to 23:59. Enter the duration of the meeting you want to schedule below, in minutes. Click the "Find Meetings!" button to calculate what time slots are available to all schedules.
                </p>
                <div>
                    <label htmlFor="duration">Meeting Duration</label>
                    <input type="number" max="1339" min="1" value={props.duration} id="duration" onChange={handleChange}></input>
                </div>
                {!props.calculated && <button type="button" id="final" onClick={props.handleFind}>Find meetings!</button>}
                {props.calculated && <button type="button" id="return" onClick={props.handleReturn}>Back to Editor</button>}
            </div>
        </div>

    )
}
