import React, { useState } from 'react';
import Meeting from './Meeting.js'
import TimeField from 'react-simple-timefield';

export default function Schedule(props) {
    const number = (props.index % 5) + 1;
    const thisSchedule = props.schedules[props.index];
    const bounds = thisSchedule.bounds;

    const [title, setTitle] = useState(`SCHEDULE ${props.index + 1}`);

    const handleAdd = () => {
        let schedules = [...props.schedules];
        schedules[props.index].meetings.push(["00:00","00:00"]);
        props.setSchedules(schedules);
    }

    const handleChange = (event, value) => {
        const time = value;
        const index = event.target.getAttribute("data-bound") === "start" ? 0 : 1;
        let schedules = [...props.schedules];
        schedules[props.index].bounds[index] = time;
        props.setSchedules(schedules);
    }

    const handleDelete = () => {
        const schedules = props.schedules.filter((elem, index) => index !== props.index);
        props.setSchedules(schedules);
    }

    const handleTitle = (e) => {
        setTitle(e.target.value);
    }

    return (
        <div className={`schedule-${number} schedule`}>
            <div className="schedule-header">
                <input type="text" value={title} onChange={handleTitle} className="schedule-title"></input>
                <div className="time-pair bounds"> 
                <TimeField data-bound="start" value={bounds[0]} onChange={handleChange}/>
                <p>to</p>
                <TimeField data-bound="end" value={bounds[1]} onChange={handleChange}/>
                </div>
            </div>
            <fieldset className="meetings-field">
                <legend>CURRENT MEETINGS</legend>
                {thisSchedule.meetings.map((elem, index) => <Meeting key={index} index={index} parentIndex={props.index} schedules={props.schedules} setSchedules={props.setSchedules} />)}
                <button type="button" className="add-meeting" onClick={handleAdd}>
                    + Add Meeting
                </button>
            </fieldset>
            <div className="delete-schedule">
            <button type="button" className="delete-schedule-button" onClick={handleDelete}><i className="fas fa-minus"></i></button>
            </div>
        </div>
    )
}