import Editor from './components/Editor.js';
import Info from './components/Info.js';
import React, { useState } from 'react';
import findMeetings from './logic.js';

function App() {

  const [schedules, setSchedules] = useState([{
    name: "schedule1",
    bounds: ["00:00", "00:00"],
    meetings: [["00:00", "00:00"]]
  }]);
  const [duration, setDuration] = useState(30);
  const [possible, setPossible] = useState([]);
  const [success, setSuccess] = useState(false);
  const [calculated, setCalculated] = useState(false);

  const handleFind = () => {
    let fullBounds = [];
    let fullMeetings = [];
    schedules.forEach(elem => {
      fullBounds = fullBounds.concat(elem.bounds);
      fullMeetings = fullMeetings.concat(elem.meetings);
    });
    const result = findMeetings(duration, fullBounds, fullMeetings);
    if (typeof result === "string") {
      setSuccess(false);
    } else {
      setSuccess(true);
    }
    setCalculated(true);
    setPossible(result);
  }

  const handleReturn = () => {
    setCalculated(false);
  }

  return (
    <div className="App" id="app">
      <Info 
        duration={duration} 
        setDuration={setDuration} 
        handleFind={handleFind} 
        possible={possible} 
        success={success} 
        calculated={calculated} 
        handleReturn={handleReturn} 
        />
      <Editor 
        schedules={schedules} 
        setSchedules={setSchedules} 
        calculated={calculated} 
        />
    </div>
  );
}

export default App;
